"use client";

import { Product } from "@/app/types/product";
import { usePathname, useRouter, useSearchParams } from "next/navigation";
import { useState } from "react";

interface ProductInteractionProps {
  selectedSize: string;
  selectedColor: string;
  product: Product;
}

export default function ProductInteration({
  selectedSize,
  selectedColor,
  product,
}: ProductInteractionProps) {
  const router = useRouter();
  const pathname = usePathname();
  const serachParams = useSearchParams();
  const [quantity, setQuantity] = useState(1);

  const handleTypeChange = (type: string, value: string) => {
    const params = new URLSearchParams(serachParams.toString());
    params.set(type, value);

    router.push(`${pathname}?${params.toString()}`, { scroll: false });
  };

  const handleChangeQuantity = (type: "decrease" | "increase") => {
    if (type === "increase") {
      setQuantity((prev) => prev + 1);
    } else {
      setQuantity((prev) => prev - 1);
    }
  };
  return (
    <>
      <div>
        <div className="space-y-3">
          <label className="block text-sm font-medium text-gray-700">
            Size
          </label>
          <div className="flex gap-3">
            {product.sizes.map((size) => (
              <div
                key={size}
                onClick={() => handleTypeChange("size", size)}
                className={`px-4 py-2  cursor-pointer border rounded-md text-sm font-medium transition-all duration-300  ${
                  selectedSize === size
                    ? "bg-gray-900 text-white border-gray-900"
                    : "bg-white text-gray-900 border-gray-300 hover:border-gray-900"
                }`}
              >
                {size}
              </div>
            ))}
          </div>
        </div>
        <div className="space-y-3">
          <label className="block text-sm font-medium text-gray-700">
            Color
          </label>
          <div className="flex gap-3">
            {product.colors.map((color) => (
              <div
                key={color}
                onClick={() => handleTypeChange("color", color)}
                className={`w-8 h-8 rounded-full border-2  cursor-pointer transition-all duration-300 ${
                  selectedColor === color
                    ? "border-gray-900 scale-110"
                    : "border-gray-300 hover:border-gray-600"
                }`}
                style={{
                  backgroundColor: color,
                }}
              />
            ))}
          </div>
        </div>
      </div>

      <div className="space-y-3">
        <label className="block text-sm font-medium text-gray-700">
          Quantity
        </label>
        <div className="flex items-center border border-gray-300 rounded-md w-fit">
          <button
            onClick={() => handleChangeQuantity("decrease")}
            className="px-3 py-2  cursor-pointer text-gray-600 hover:text-gray-900 transition-colors"
          >
            <Minus size={16} />
          </button>
          <span className="px-4 py-2 text-gray-900 font-medium min-w-[3rem] text-center">
            {/* {quantity} */}
          </span>
          <button
            onClick={() => handleChangeQuantity("increase")}
            className="px-3 py-2 cursor-pointer text-gray-600 hover:text-gray-900 transition-colors"
          >
            <Plus size={16} />
          </button>
        </div>
      </div>
    </>
  );
}
